<template>
  <div>

    <div class="main" @click="toggle" :class="{ 'text-muted': selected === placeholder }">
      {{ selected }}
      <span class="arrow arrow-up" v-show="isOpen"></span>
      <span class="arrow arrow-down" v-show="!isOpen"></span>
    </div>

    <div class="list" v-show="isOpen">

      <input ref="text_filter" type="text" v-model.trim="search">

      <ul>
        <li v-for="(index, option) in filteredOptions"
            :data-index="index"
            :data-value="stringify(option)"
            :class="{ active: index === selectedIndex }"
            @click="selectMe($event)"
        >
          {{ option[label] }}
        </li>
      </ul>

      <div v-show="!filteredOptions.length" class="no-data">
        {{ nodata }}
      </div>

    </div><!-- \.list -->

  </div>
</template>

<script>

  export default {

    name: 'Vuelist',

    // filters: {
    //   stringify(object) {
    //     return JSON.stringify(object, null, 2);
    //   },
    // },

    props: {

      autoclose: {
        type: Boolean,
        default: true,
      },

      open: {
        type: Boolean,
        default: false
      },

      placeholder: {
        type: String,
        default : '',
      },

      options: {
        type: Array,
        required: true,
        default: () => {
          return [];
        },
      },

      label: {
        type: String,
        default: 'label'
      },

      nodata: {
        type: String,
        default: 'No data'
      },

      value: {
      },

    },


    data() {
      return {
        search: '',
        isOpen: true,
        selectedIndex: null,
        currentValue: null,
      }
    },


    watch: {
      open(val) {
        this.isOpen = val;
      }
    },


    computed: {

      selected() {
        return this.value ? this.value[this.label] : this.placeholder;
      },


      filteredOptions() {
        return this.options
          .filter(item => item[this.label].toLowerCase().indexOf(this.search) > -1)
      },

    },


    methods: {

      stringify(object) {
        return JSON.stringify(object, null, 2);
      },

      /**
       * Clear the input if exists, then close the list.
       */
      clearOrClose() {
        if (this.search) {
          return this.search = '';
        }

        this.close();
      },


      /**
       * Close the list
       */
      close() {
        this.isOpen = false;
      },


      /**
       * Open/close list toggle
       */
      toggle() {
        this.isOpen = ! this.isOpen;
        this.$refs.text_filter.focus();
        this.$nextTick(() => {
          this.$refs.text_filter.focus();
        });
      },


      /**
       * When select the item from list
       */
      selectMe($event) {
        this.selectedIndex = parseInt($event.target.dataset.index);
        this.currentValue = JSON.parse($event.target.dataset.value);

        this.$emit('change', this.currentValue);

        this.isOpen = !this.autoclose;
      }

    }

  }
</script>

<style scoped>

.list {
  position: relative;
  background: white;
  border: 1px solid #cecece;
  border-top: none;
  padding: 4px;
}

.main {
  padding: 4px 8px;
  border: 1px solid #cecece;
  background-color: #fefefe;

  position: relative;
  cursor: default;

  /* Disable selection text */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.arrow {
  position: absolute;
  top: 14px;
  right: 10px;
}

.arrow-up {
  border-top: 0 solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 5px solid #888;
  border-left: 5px solid transparent;
}

.arrow-down {
  border-top: 5px solid #888;
  border-right: 5px solid transparent;
  border-bottom: 0 solid transparent;
  border-left: 5px solid transparent;
}

input[type="text"] {
  box-sizing: border-box;
  padding: 6px;
  border: 1px solid #cecece;
  width: 100%;
}

input:focus {
  outline: none;
}

ul {
  padding: 0;
  margin: 0;
  max-height: 200px;
  overflow: auto;
}

ul li {
  background-color: #f0f0f0;
  padding: 4px 8px;
  list-style: none;

  /* Disable selection text */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

ul li:hover,
ul li.active:hover {
  background: #ddd;
  cursor: default;
}

ul li.active {
  background: #d0d0d0;
}

.no-data {
  padding: 4px;
  color: #818a91;

  /* Disable selection text */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

</style>



