<template>
  <div>
    <div class="row">
      <div class="col-md-6">

        <vuelist placeholder="Select an item from list ..."
              @change="doSomething"
              :options="list.categories"
              label="text"
              :value="selected"
              :open="isOpen"
              :autoclose="false"
              nodata="Data kosong"
        ></vuelist>

      </div>
      <div class="col-md-6">
        <button class="btn btn-sm btn-default" @click="clear1">Clear</button>
        <button class="btn btn-sm btn-default" @click="fill">Fill</button>
        <button class="btn btn-sm btn-default" @click="openToggle">Open/Close Toggle</button>

        <dl v-if="selected" style="margin: 10px 0; padding: 10px; background: #f0f0f0;">
          <dd>ID: {{ selected.id }}</dd>
          <dd>Name: {{ selected.text }}</dd>
          <dd>Brand: {{ selected.brand }}</dd>
          <dd>Price: IDR {{ selected.price }}</dd>
        </dl>
      </div>
    </div>

    <hr>

    <div class="row">
      <div class="col-md-6">
        <vuelist placeholder="Select your action!"
                 @change="doSomething2"
                 :options="list.actions"
                 :value="selected2"
                 :open="true"
                 :autoclose="true"
                 nodata="Data kosong"
        ></vuelist>
      </div>
      <div class="col-md-6">
        <button class="btn btn-sm btn-default" @click="clear2">Clear</button>
        <dl v-if="selected2" style="margin: 10px 0; padding: 10px; background: #f0f0f0;">
          <dd>Action: {{ selected2.label }}</dd>
        </dl>
      </div>
    </div>

  </div>
</template>

<script>
  import Vuelist from './components/List.vue';

  export default {

    name: 'App',

    components: {
      vuelist: Vuelist
    },

    data() {
      return {
        isOpen: true,

        selected: null,
        selected2: null,

        list: {
          categories: require('./data.json'),
          actions: []
        }
      }
    },


    methods: {

      doSomething(val) {
        this.selected = val;

        this.list.actions = [
          { label: 'Buy' },
          { label: 'Sell' },
          { label: 'Ask' },
          { label: 'Throw' },
          { label: 'Repair' }
        ];
      },

      doSomething2(val) {
        this.selected2 = val;
      },

      openToggle() {
        this.isOpen = !this.isOpen;
      },

      clear1() {
        this.selected = '';
        this.list.categories = [];
      },

      clear2() {
        this.selected2 = '';
        this.list.actions = [];
      },


      fill() {
        this.list.categories = require('./data.json');
      }

    }

  }
</script>



